@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .form-heading {
    @apply text-lg text-white font-semibold;
  }

  .form-subheading {
    @apply text-base text-white opacity-50;
  }

  .text-input{
    @apply focus:outline-none focus:ring-1 focus:border-green-400 focus:border-2 focus:ring-green-400  rounded-md w-full py-2 text-right pr-4 text-white bg-black bg-opacity-20 text-lg font-medium text-opacity-90
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
